export class Solution {
    constructor(view: any) {
        console.log("Let's learn Typescript for digital mapping and visualization!!");
        console.log("Typescript, Graph data Structure");
        console.log("=================================");
        // Codepan
        // Typescript: https://codepen.io/NJStudio/details/RwWxGXo
        // Javascript: https://codepen.io/NJStudio/details/bGdozYY

        //           N0
        //          /  \
        //        N1    N2
        //        /\    /\
        //      N3 N4  N5 N6

        let nodes: Node[] = [];

        for (let i = 0; i < 7; ++i) {
            nodes.push(new Node(i))
        }

        nodes[0].push(nodes[1]);
        nodes[0].push(nodes[2]);

        nodes[1].push(nodes[3]);
        nodes[1].push(nodes[4]);

        nodes[2].push(nodes[5]);
        nodes[2].push(nodes[6]);

        // nodes[3].push(nodes[6]);

        // ........................................... BFS
        let num = 0;
        let outSortedBFS = []
        const bfs = (nodes: Node[]) => {
            let queue: Node[] = [];
            nodes[0].visit = true;

            queue.push(nodes[0])
            outSortedBFS.push(nodes[0])

            while (queue.length > 0) {
                let current = queue.shift();

                for (let i = 0; i < current.connection.length; ++i) {
                    let other = current.connection[i];
                    if (!other.visit) {
                        other.visit = true;
                        outSortedBFS.push(other);
                        queue.push(other);
                    }
                }
                if (num++ > 100) { console.log('safeguard'); return; }
            }
            return outSortedBFS;
        }
        bfs(nodes);
        console.log("BFS!", outSortedBFS)
        console.log('done');

        // ........................................... DFS

        nodes = [];
        for (let i = 0; i < 7; ++i) {
            nodes.push(new Node(i))
        }

        nodes[0].push(nodes[1]);
        nodes[0].push(nodes[2]);

        nodes[1].push(nodes[3]);
        nodes[1].push(nodes[4]);

        nodes[2].push(nodes[5]);
        nodes[2].push(nodes[6]);

        let outSortedDFS = []
        const dfs = (node: Node) => {
            if (node.visit) { return; }
            node.visit = true;
            outSortedDFS.push(node);
            for (let i = 0; i < node.connection.length; ++i) {
                dfs(node.connection[i])
            }
        }

        dfs(nodes[0]);
        console.log("DFS!", outSortedDFS)
        console.log('done');


        // ........................................... DFS






        aStartPropagation()


    }
    public destroy() {

    }
}

class Node {
    public visit: boolean = false;
    public id: number = -1;
    public connection: Node[] = [];
    constructor(id: number) {
        this.id = id;
    }
    public push(other: Node) {
        this.connection.push(other);
        other.connection.push(this);
    }
}

export const aStartPropagation = () => {
    // const pixelMap = new PixelMap();
    //           N0
    //          /  \
    //        N1    N2
    //        /\    /\
    //      N3 N4  N5 N6
    //      /
    //     N7

    let n0 = new NodeAStart(0, 0, 0);
    let n1 = new NodeAStart(-1, 1, 1);
    let n2 = new NodeAStart(1, 1, 2);
    let n3 = new NodeAStart(-2, 2, 3);
    let n4 = new NodeAStart(-1, 2, 4);
    let n5 = new NodeAStart(1, 2, 5);
    let n6 = new NodeAStart(2, 2, 6);
    let n7 = new NodeAStart(-2, 3, 7);

    n0.neighbors.push(n1, n2);
    n1.neighbors.push(n0, n3, n4);
    n2.neighbors.push(n0, n5, n6);
    n3.neighbors.push(n1);
    n4.neighbors.push(n1);
    n5.neighbors.push(n2);
    n6.neighbors.push(n2);
    n7.neighbors.push(n3);


    
    console.log('graph.gePath', AStart.gePath(n7, n6, AStart.TYPE.Euclidean));
}

export class AStart {
    public static TYPE = {
        Manhattan :'Manhattan  ',
        Euclidean : 'Euclidean ',
    }
    public static strength = 0.01;
    public static distanceManhattan = (x0: number, y0: number, z0: number, x1: number, y1: number, z1: number) => {
        return Math.abs(x0 - x1) + Math.abs(y0 - y1) + Math.abs(z0 - z1);
    }
    public static distanceEuclidean = (x0: number, y0: number, z0: number, x1: number, y1: number, z1: number) => {
        return Math.sqrt((x0 - x1) * (x0 - x1) + (y0 - y1) * (y0 - y1) + (z0 - z1) * (z0 - z1));
    }
    public static gePath = (startNode, endNode, type: string) => {
        this.typeHeuristic = this.distanceEuclidean;
        if (type === AStart.TYPE.Manhattan ) {
            this.typeHeuristic = this.distanceManhattan;
        }

        const openNodes = [startNode];
        const closedNodes: NodeAStart[] = [];
        let result = undefined;
        let count = 100;
        while (count--) {
            result = this.propagation(openNodes, closedNodes, endNode);
            if (result !== undefined && result.length) {
                return result;
            }
        }
        return result;
    }

    private static path: NodeAStart[] = [];
    private static propagation = (openNodes, closedNodes, endNode) => {
        if (openNodes.length > 0) {
            // console.log(openNodes)
            //find the lowest f in open nodes
            let lowestF = 0;
            for (let i = 0; i < openNodes.length; i++) {
                if (openNodes[i].f < openNodes[lowestF].f) {
                    lowestF = i;
                }

                if (openNodes[i].f === openNodes[lowestF].f) {
                    if (openNodes[i].g > openNodes[lowestF].g) {
                        lowestF = i;
                    }
                }
            }
            let current: NodeAStart = openNodes[lowestF];

            if (current === endNode) {
                //reached the end node so wait a sec and restart
                return [ current, current.previous, ...this.path];
            }

            //calculate path
            this.path = [];
            let temp = current;
            while (temp.previous) {
                this.path.push(temp.previous);
                temp = temp.previous;
            }
            // console.log(this.path.length);

            openNodes.splice(openNodes.indexOf(current), 1);
            closedNodes.push(current);

            let neighbors = current.neighbors;
            for (let i = 0; i < neighbors.length; i++) {
                let neighbor = neighbors[i];

                if (!closedNodes.includes(neighbor) && !neighbor.blocked) {
                    let tempG = current.g + this.heuristic(neighbor, current) * this.strength; 

                    if (!openNodes.includes(neighbor)) {
                        openNodes.push(neighbor);
                    }
                    else {
                        continue;
                    }

                    neighbor.g = tempG;
                    neighbor.h = this.heuristic(neighbor, endNode);
                    neighbor.f = neighbor.g + neighbor.h;
                    neighbor.previous = current
                }
            }
            return undefined;
        } else {
            return this.path;
        }
    }
    private static typeHeuristic: (x0: number, y0: number, z0: number, x1: number, y1: number, z1: number) => number;
    private static heuristic = (a: NodeAStart, b: NodeAStart) => {
        let d = 0;
        return this.typeHeuristic(a.x, a.y, a.z, b.x, b.y, b.z);
    }
    
}
export class NodeAStart {
    public id = -1;
    
    public f = 0;
    public h = 0;

    public g = 0;

    public x = 0;
    public y = 0;
    public z = 0;
    public neighbors: NodeAStart[] = [];
    public previous = undefined;
    public blocked = false;
    constructor(x: number, y: number, id: number) {
        this.id = id;
        this.x = x;
        this.y = y;
    }
}

